.school {
	position: relative;
	padding: 150px 0;
	.opac {
		position: absolute;
		width: 400px;
		height: 346.67px;
		top: 26%;
		right: 10%;
		opacity: 0.1;
	}
	&__border {
		color: $second;
		font-size: 18px;
		font-weight: 400;
		text-align: left;
		padding: 86px 90px 76px 90px;
		width: 638px;
		height: 405px;
		border-bottom: 10px solid $accent;
		border-right: 10px solid $accent;
		border-left: 10px solid $accent;
		&:after,&:before {
			content: "";
			display: block;
			position: absolute;
			top: 140px;
			left: 85px;
			width: 70px;
			height: 0px;
			border-bottom: 10px solid $accent;
		}
		&:before {
			top: 140px;
			left: 533px;
			width: 190px;
		}

	}
	&__title {
		position: absolute;
		top: 16%;
		left: 12%;
		color: $second;
		font-size: 48px;
		font-weight: 700;
		text-align: left;
	}
}