.events {
	&__wrapper {
		display: flex;
		justify-content: space-between;
		margin-top: 100px;
	}
	&__item {
		width: 306px;
		img {
			width: 100%;
		}
	}
	&__title {
		margin-top: 30px;
		color: $second;
		font-size: 24px;
		font-weight: 700;
		text-align: center;
	}
	&__descr {
		color: $second;
		font-size: 16px;
		font-weight: 400;
		text-align: left;
		&-mt25 {
			margin-top: 25px;
		}
	}
}