.table {
	padding-bottom: 150px;
	&__table {
		border-collapse: separate;
		border-spacing: 0px 40px;
	}
	&__wrapper {
		margin-top: 30px;
		display: flex;
		justify-content: space-between;
		margin-bottom: 101px;
	}
	&__cours {
		color: $second;
		font-size: 24px;
		font-weight: 700;
		text-align: center;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $accent;
		}
	}

&__table {
	width: 1076px;
	margin: 0 auto;
}
&__tg {
	color: rgb(51, 51, 51);
	font-size: 18px;
	font-weight: 400;
	text-align: left;
	&:nth-child(1) {
		width: 78px;
	}
	&:nth-child(2) {
		width: 300px;
	}
	&:nth-child(3) {
		width: 500px;
	}
	&-tt {
		color: rgb(51, 51, 51);
		font-size: 20px;
		font-weight: 700;
		text-align: left;
	}
}
.back {
	min-height: 48px;
	background: rgba(51, 51, 51, 0.05);
}

&__tg {
	border-top: 1px solid rgba(51, 51, 51, 0.2);
	padding-top: 10px;
	&-non {
		border: none;
	}
}
&__tg-tt {
	border: none;
}
}