.swiper {
	margin-top: 20px;
	.swiper-slide {
		position: relative;
		img {
			margin: 0 auto;
			width: 100%;
			min-height: 700px;
		}
	}
	.swiper-inner {
		position: absolute;
		top: 126px;
		left: 200px;
		width: 526px;
		min-height: 280px;
		padding: 15px 30px;
		background-color:rgba($color: #000000, $alpha: .56);
		color: rgb(255, 255, 255);
		font-size: 40px;
		font-weight: 700;
		text-transform: uppercase;
		text-align: left;
	}

	.swiper-button-prev {
		left: 3%;
	}
	.swiper-button-next {
		right: 3%;
	}
}