.btn {
	width: 143px;
	height: 40px;
	background-color: $accent;
	color: rgb(255, 255, 255);
	font-size: 14px;
	line-height: 35px;
	font-weight: 700;
	text-align: center;
	border: none;
	transition: 0.5s all;
	&:hover {
		box-shadow: 3px 3px 20px rgb(199, 1, 1);
		border: none;
	}
	&__slider {
		position: absolute;
		bottom: 156px;
		left: 200px;
	}
	&__table {
		margin-top: 52px;
		width: 175px;
		height: 68px;
		font-size: 14px;
		font-weight: 400;
		line-height: 14px;
		padding: 12px 20px;
		&_2 {
			margin-left: 164px;
		}
	}
	&_mini {
		height: 25px;
		line-height: 14px;
		width: 75px;
	}
	&__center {
		display: block;
		margin: 0 auto;
		margin-top: 45px;
	}
}