.header {
	padding-top: 20px;
	&__wrapper {
		display: flex;
		justify-content: space-between;
		
	}
	&__title {
		color: $accent;
		font-size: 14px;
		font-weight: 700;
		text-align: left;
		text-transform: uppercase;
		width: 217px;
	}
	&__item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 500px;
	}
	&__items {
		display: flex;
		align-items: center;
		.img {
			width: 19px;
			height: 19px;
			margin-right: 10px
		}
		.animate__animated {
			animation-iteration-count: infinite;
			animation-delay: 0s;

		}



	}
	&__text {
		color:$second;
		font-size: 14px;
		font-weight: 400;
		text-align: left;
		text-decoration: none;
		transition: 0.5s all;
		&:hover {
			color:$accent;
		}
	}

	&__subwrapper {
		display: flex;
		justify-content: space-between;
	}
	&__menu {
		margin-top: 40px;
		display: flex;
		gap: 20px;
		list-style-type: none;
		color: rgb(51, 51, 51);
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		li {
			transition: 0.5s all;
		}
		li:hover {
			color: $accent;
			cursor: pointer;
		}
	}
	&__logo {
		img {
			vertical-align: super;
			height: 65px;
		}
	}

}