.footer {
	min-height: 588px;
	padding-top: 60px;
	margin-top: 100px;
	background-color: rgba(51, 51, 51, 0.06);
	&__wrapper {
		display: flex;
		justify-content: space-between;
	}
	&__item {
		width: 221px;
	}
	&__title {
		color: $second;
		font-size: 13px;
		text-align: left;
		font-weight: 700;
		text-transform: uppercase;
	}
	&__one {
		margin-top: 40px;
	}
	&__items {
		a:nth-child(2) {
			margin-left: 25px;
			color:$second;
			font-size: 14px;
			font-weight: 400;
			text-align: left;
		}
	}
	&__items-wrapper {
		width: 180px;
		margin-top: 42px;
		display: flex;
		justify-content: space-between;
	}
	&__title-onas {
		color: $second;
		font-size: 20px;
		font-weight: 700;
		text-align: left;
		&-down {
			margin-top: 40px;
		}
	}
	&__ul {
		margin-top: 25px;
		list-style-type: none;
		padding-left: 0;
		li {
			margin-top: 15px;
			width: 200px;
			a {
				color: $second;
				font-size: 14px;
				font-weight: 400;
				text-align: left;
				text-decoration: none;
			}
		}
	}
	&__end-wrapper {
		display: flex;
		justify-content: space-between;
		&-text {
			color: rgba(51, 51, 51, 0.5);
			font-size: 14px;
			font-weight: 400;
			line-height: 140%;
			letter-spacing: 0%;
			text-align: right;
		}
	}

}